<template>
  <div class="dialog">
    <md-dialog :md-active.sync="showDialog">
      <md-dialog-title>
        Добавление товара
        <span @click="showDialog = false">
            <md-icon class="fa fa-times dialog__close"></md-icon>
        </span>
      </md-dialog-title>
      <div class="dialog__content">
        <md-field>
            <label>Название</label>
            <md-input v-model="item.name"></md-input>
        </md-field>
        <md-field>
            <label>Цена</label>
            <md-input v-model="item.price" type="number"></md-input>
        </md-field>
         <md-field>
            <label>Количество</label>
            <md-input v-model="item.count" type="number"></md-input>
        </md-field>
        <!-- <md-avatar class="image">
            <img :src="item.imagePreview" v-if="item.imagePreview.length > 0">
            <img src="../assets/images/no-image.svg" v-else>
        </md-avatar>
        <div class="upload">
            <button class="upload__button">ЗАГРУЗИТЬ ФОТО</button>
            <input type="file" @change="imagePreview"/>
        </div> -->
      </div>
      <md-dialog-actions>
        <md-button class="md-raised md-green" @click="addItem">Сохранить</md-button>
      </md-dialog-actions>
    </md-dialog>
  </div>
</template>

<script>
import itemsService from '../services/itemsService'
import { eventBus } from '../main'

export default {
  data: () => ({
    showDialog: false,
    item: {
      name: '',
      price: '',
      count: ''
      // imagePreview: '',
      // image: '',
      // itemImageId: ''
    }
  }),
  created () {
    eventBus.$on('showDialog', (showDialog) => {
      this.showDialog = showDialog
    })
  },
  methods: {
    // ЗАГРУЗКА ФОТО
    // imagePreview (event) {
    //   let input = event.target
    //   if (input.files && input.files[0]) {
    //     let reader = new FileReader()
    //     reader.onload = (e) => {
    //       this.item.imagePreview = e.target.result
    //     }
    //     reader.readAsDataURL(input.files[0])
    //     let formData = new FormData()
    //     formData.append('image', input.files[0])
    //     this.item.image = formData
    //     this.addImage()
    //   }
    // },
    // async addImage () {
    //   try {
    //     let image = this.item.image
    //     const response = await itemsService.addItemImage(image)
    //     this.item.itemImageId = response.data.responseData.id
    //   } catch (error) {
    //     console.log(error.response.data.message)
    //   }
    // },
    async addItem () {
      try {
        var credentials = {
          name: this.item.name,
          price: this.item.price,
          count: this.item.count
        }
        const response = await itemsService.addItem(credentials)
        if (response.status === 200) {
          this.showDialog = false
          // GoodsVue.methods.fetchData()
        }
      } catch (error) {
        console.log(error.response.data.message)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.md-dialog {
    width: 40%;
    overflow-y: auto;
}

.md-dialog-actions {
    padding: 8px 24px;
}

.md-button .md-ripple {
    padding: 0 20px !important;
}

.md-avatar {
    border-radius: 0;
    width: 110px;
    height: 110px;
    position: relative;
    background: #F3F3F4;

    img {
        height: auto;
    }

    .md-icon {
        right: 0;
        top: 0;
        left: auto;
        transform: none;
    }
}
</style>



